export default class TopBar {
  static _toggleMenuHandler(e) {
    e.preventDefault();
    e.stopPropagation();

    this.topBar.classList.toggle("top-bar_close");
  }

  static init() {
    const switchButton = document.querySelector(".js-top-bar__switch-button");
    this.topBar = document.querySelector(".js-top-bar");

    switchButton.addEventListener("click", this._toggleMenuHandler.bind(this));

    this.topBar.classList.toggle("top-bar_close");
  }
}
