export default class LoginForm {
  static init() {
    this.classId = ".js-login-form__form";
    this.rootEl = document.querySelector(this.classId);

    if (this.rootEl === null) {
      throw new Error(`LoginForm.init element with class ${this.classId} not found`);
    }

    this._initEvents();
  }

  static _initEvents() {
    this.rootEl.addEventListener("submit", this._submitFormHandler);
  }

  static _submitFormHandler(e) {
    e.stopPropagation();
    e.preventDefault();

    /* eslint-disable no-console */
    console.log("login form submit");
  }
}
